<template>
    <div>
        <div v-if="jobs.length > 0">
            <JobCard v-for="job in activeJobs" :key="job._id" :job="job" />
            <div>
                <b-pagination
                    v-model="currentPage"
                    @change="scrollToTop"
                    class="mt-4"
                    :total-rows="noOfJobs"
                    :per-page="perPage"
                    pills
                    align="center"
                    :limit="5"
                    aria-controls="my-table"
                ></b-pagination>
            </div>
        </div>
        <div
            v-else-if="jobs.length === 0 && $store.state.jobs.jobs.length > 0"
            style="min-height: 200px"
        >
            <p class="h5">Leider gibt es hierzu keine Ergebnisse.</p>
        </div>
        <div v-else>
            <JobCardPlaceholder class="mb-3" />
            <JobCardPlaceholder class="mb-3" />
            <JobCardPlaceholder class="mb-3" />
        </div>
    </div>
</template>

<script>
    import JobCard from "@/components/ui/JobCard.vue";
    import JobCardPlaceholder from "@/components/ui/JobCardPlaceholder.vue";
    export default {
        name: "JobboardList",
        props: ["jobs"],
        components: {
            JobCard,
            JobCardPlaceholder
        },
        data() {
            return {
                perPage: 25,
                currentPage: 1
            };
        },
        computed: {
            noOfJobs() {
                return this.jobs.length;
            },
            activeJobs() {
                return this.jobs.slice(
                    this.currentPage * this.perPage - this.perPage,
                    this.currentPage * this.perPage
                );
            }
        },
        methods: {
            scrollToTop() {
                window.scrollTo({ top: 0, behavior: "smooth" });
            }
        }
    };
</script>
